.passcode-page {
  font-family: 'Montserrat', sans-serif; }
  .passcode-page p {
    font-weight: bold;
    text-align: center; }
  .passcode-page .time {
    margin-top: 60px;
    color: #ea5535;
    font-size: 20px;
    margin-top: 40px; }
  .passcode-page .title {
    font-size: 20px;
    margin: 20px 0;
    color: #363636; }
  .passcode-page .name {
    color: #008eff;
    font-size: 20px;
    margin-top: 40px;
    margin-bottom: 0; }
  .passcode-page .tip {
    color: #343434;
    margin-top: 10px;
    font-size: 22px; }
  .passcode-page img {
    width: 60%;
    display: block;
    margin: 0 auto; }
